import $T from '@/common/lib/time.js'
export default{
	data(){
		return {
			id:'',
			cateIndex:0,
			cateList:[
				{name:"全部",value:""},
				{name:"好评",value:"/good"},
				{name:"中评",value:"/middle"},
				{name:"差评",value:"/bad"}
			],
			total:0,
			good_rate:0,
			list:[],
			// 1.上拉加载更多 2.加载中... 3.没有更多了
			loadtext:'上拉加载更多'
		}
	},
	onLoad(option) {
		if(option.id){
			this.id = option.id
			this.init()
		}
	},
	// 上啦加载
	onReachBottom() {
		if(this.loadtext !== '上拉加载更多') return
		this.page++
		this.loadtext = '加载中...'
		this.getData()
	},
	// 下拉刷新
	onPullDownRefresh(){
		this.getData((res)=>{
			uni.hideLoading()
			uni.showToast({
				title:"刷新成功",
				icon:'none'
			})
			uni.stopPullDownRefresh()
		},true)
	},
	filters:{
		formaTime(value) {
			return $T.gettime(value)
		},
		rating(value) {
			if(value === 3){
				return '中评'
			} else if(value >3) {
				return '好评'
			} else {
				return '差评'
			}
		}
	},
	methods:{
		init(){
			this.$H.get(`/goods/${this.id}/comments`).then(res=>{
				this.total = res.total
				this.good_rate = res.good_rate
				this.list = res.list
			})
		},
		getData(callback = false, refresh = false){
			let requestType = this.cateList[this.cateIndex].value
			this.$H.get(`/goods/${this.id}/comments${requestType}?page=${this.page}`).then(res=>{
				this.total = res.total
				this.good_rate = res.good_rate
				this.list = !refresh ? [...this.list,...res.list] : [...res.list]
				this.loadtext = res.list.length < 10 ? '没有更多数据了' : '上拉加载更多'
			})
			
			if(typeof callback === 'function') {
				callback(true)
			}
		}, 
		cateChange(index){
			this.cateIndex = index
			uni.showLoading({
				title:"加载中..."
			})
			this.getData((res)=>{
				uni.hideLoading()
				uni.showToast({
					title:"加载成功",
					icon:'none'
				})
			},true)
		}
	}
}
